.flex {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    &--nowrap {
        flex-wrap: nowrap;
    }
    &--grow {
        flex-grow: 1;
        flex-shrink: 1;
    }
    &--vertical {
        flex-direction: column;
    }
    &--justify {
        &-center {
            justify-content: center;
        }
    }
    &--align {
        &-top {
            align-items: top;
        }
        &-center {
            align-items: center;
        }
    }
}