@import "menu";
@import "form";

.m { // margin
    &--l {
        margin-left: auto;
    }
    &--t {
        margin-top: auto!important;
    }
}

.layer {
    &--bg {
        z-index: 1;
    }
    &--mid {
        z-index: 50;
    }
    &--top {
        z-index: 999;
    }
}

.align {
    &--center {
        text-align: center;
        justify-content: center;
    }
}