@import '../../assets/scss/variables';

.wallet {
    &__overview {
        background-color: $white;
        border: 1px solid $gray;
        border-radius: 8px;
        padding: 1em;
        .description {
            flex-grow: 1;
            flex-shrink: 1;
            padding-left: 1em;
            .MuiOutlinedInput-root {
                border-width: 1px;
            }
            &__content {
                color: $black;
            }
        }
        .avatar {
            margin-right: 1em;
        }
    }
    &__image {
        width: 100px!important;
        height: 100px!important;
    }

    &__tokens {
        .MuiCard {
            &Content-root {
                padding: 1em;
            }
            &Media-root {
                width: 100px;
                width: calc(100px + 2em);
            }
        }
    }
}